@import url(https://fonts.googleapis.com/css2?family=Pattaya&display=swap);
body {
  background: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.4)),
    url("https://4kwallpapers.com/images/wallpapers/waves-blue-gradient-background-stock-3840x2160-1166.jpg");
  width: 100%;
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  overflow: hidden;
}

.heading {
  font-size: 18px;
  text-align: center;
  color: white;
}

* {
  font-family: "Pattaya", sans-serif;
}

.main {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

form {
  top: 27%;
  left: 50%;
  position: absolute;
  -webkit-transform: translate(-103%, -50%);
          transform: translate(-103%, -50%);
}

input[type="text"] {
  width: 200%;
  height: 7vh;
  background: rgba(255, 255, 255, 0.1);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  color: white;
  text-align: right;
  font-size: 14.5px;
  font-weight: 500;
  letter-spacing: 2px;
}

.keypad-container {
  top: 60%;
  left: 50%;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin: 0 auto;
  width: 356px;
  text-align: center;
  border: 1px solid black;
  background: rgba(255, 255, 255, 0.1);
  -webkit-backdrop-filter: blur(6px);
          backdrop-filter: blur(6px);
}

.keypad {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(70px, auto);
}

button {
  background: none;
  color: white;
  font-size: 22px;
  font-weight: bold;
  margin: 7px;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background: linear-gradient(
    rgba(20, 137, 233, 0.39),
    rgba(24, 192, 156, 0.705)
  );
}

#clear {
  grid-column: 1/3;
}

